import { supabase } from "../../lib/supabaseClient";
import { useEffect, useState, useCallback } from "react";
import styled from "styled-components";

import useMedia from "../../hooks/useMedia";
import VideoWrapper from "./VideoWrapper";
import Final from "./Final";
import Spinner from "./Spinner";

const OuterWrap = styled.div`
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
`;

const InnerWrap = styled.div`
    width: fit-content;
    display: flex;
    justify-content: center;
    flex-direction: column;
`;

const FeedTitle = styled.h1`
    margin: 0 2rem;
    font-size: calc(1rem + 1vw);
    cursor: pointer;
    &:after {
        content: " ";
        display: block;
        margin-top: 1rem;
        height: 2px;
        width: 200px;
        background-color: #ffcf00;
    }
`;

function Feed() {
    const [isLoading, setIsLoading] = useState(false);
    const [fetchOptions, setFetchOptions] = useState({ limit: 15, offset: 0, sortBy: { column: "updated_at", order: "desc" } });
    const [endReached, setEndReached] = useState(false);
    const [tempVideos, setTempVideos] = useState([]);
    const [videos, setVideos] = useState([]);

    const videoAmount = useMedia(
        ["(min-width: 2000px)", "(min-width: 1600px)", "(min-width: 1400px)", "(min-width: 968px)", "(min-width: 668px)", "(max-width: 500px)"],
        [6, 5, 4, 3, 2, 1],
        1
    );

    const sanitizeVideos = (data) => {
        const tempData = [...new Set(data)]; // remove any possible duplicates

        tempData?.forEach((video) => {
            const { publicURL, error } = supabase.storage.from("cmemes").getPublicUrl(video.name);
            if (error) throw new Error(error);

            setTempVideos((lastVideos) => [
                ...lastVideos,
                {
                    name: video.name,
                    url: publicURL,
                    date: video.updated_at,
                },
            ]);
        });
    };

    const getVideos = useCallback(
        async (options) => {
            if (!isLoading) {
                setIsLoading(true);

                const { data, error } = await supabase.storage.from("cmemes").list(undefined, options);
                if (error) throw new Error(error);

                setFetchOptions((lastOptions) => ({ ...lastOptions, offset: lastOptions.offset + 15 }));
                sanitizeVideos(data);

                if (tempVideos) setIsLoading(false);
                if (data.length === 0) setEndReached(true);
            }
        },
        [isLoading, tempVideos]
    );

    const refreshVideos = (options) => {
        setTempVideos([]);
        setVideos([]);
        const currOptions = Object.assign({}, options);
        currOptions.limit = currOptions.offset;
        currOptions.offset = 0;

        getVideos(currOptions);
    };

    useEffect(() => {
        // Initial fetch
        let mounted = true;
        mounted && getVideos(fetchOptions);
        return () => {
            mounted = false;
        };
    }, []);

    useEffect(() => {
        // Organize videos based on viewport
        let mounted = true;

        // Divide in chunks
        // Returns object[]: [{v: "video1", v: "video2"}], [{v: "video1", v: "video2"}]
        if (tempVideos.length >= 1 && mounted) {
            tempVideos.reduce((all, one, index) => {
                const ch = Math.floor(index / videoAmount);
                all[ch] = [].concat(all[ch] || [], one);
                console.log(all)
                setVideos(all);
                return all;
            }, []);
        }

        return () => {
            mounted = false;
        };
    }, [videoAmount, tempVideos]);

    useEffect(() => {
        // Fetch more videos if scrolled to the end of the page
        let mounted = true;
        const checkScrollPos = () => {
            if (window.innerHeight + window.scrollY >= document.body.offsetHeight && !isLoading && !endReached) {
                getVideos(fetchOptions);
            }
        };

        if (mounted) document.addEventListener("scroll", checkScrollPos, { passive: true });

        return () => {
            document.removeEventListener("scroll", checkScrollPos);
            mounted = false;
        };
    }, [endReached, isLoading]);

    return (
        <>
            <OuterWrap>
                <InnerWrap>
                    <FeedTitle onClick={() => refreshVideos()}>Memes populares</FeedTitle>
                    {videos ? videos.map((rowVids, i) => <VideoWrapper videos={rowVids} videoAmount={videoAmount} key={i} />) : null}
                </InnerWrap>
            </OuterWrap>
            {isLoading && <Spinner />}
            {endReached && <Final />}
        </>
    );
}

export default Feed;
